export default "<template>\n  <div>\n    <div class=\"section\">\n      <h1>Guards</h1>\n\n      <pre><code>import { Guard } from 'streamlabs-beaker';\n\ncomponents: {\n  Guard\n}</code></pre>\n    </div>\n\n    <div class=\"section\">\n      <h2>Text Guard</h2>\n\n      <DemoSection title=\"Text Guard\" :code=\"demoCode\">\n        <template #components>\n          <Guard class=\"guard-margin\" type=\"alt\" value=\"Text\" />\n        </template>\n      </DemoSection>\n    </div>\n\n    <div class=\"section\">\n      <h2>Input Guard</h2>\n\n      <DemoSection title=\"Input Guard\" :code=\"demoCode\">\n        <template #components>\n          <Guard type=\"input\">\n            <div slot=\"content\">\n              <TextInput\n                slot=\"input\"\n                label=\"Text input\"\n                type=\"text\"\n                :placeholder=\"textInputPlaceholder\"\n                v-model=\"textInputValue\"\n                name=\"textExample\"\n              />\n            </div>\n          </Guard>\n        </template>\n      </DemoSection>\n    </div>\n\n    <!-- <div class=\"section\">\n      <h2>Text Guard</h2>\n\n      <DemoSection title=\"Text Guard\" :code=\"demoCode\">\n        <template #components>\n          <GuardNew :value=\"textInputValue\" />\n        </template>\n      </DemoSection>\n    </div>-->\n\n    <table class=\"docs-table\">\n      <thead>\n        <tr>\n          <th>Prop</th>\n          <th>Type</th>\n          <th>Default</th>\n          <th>Description</th>\n        </tr>\n      </thead>\n      <tbody>\n        <tr>\n          <td>value</td>\n          <td>string</td>\n          <td>null</td>\n          <td>Text Value</td>\n        </tr>\n        <tr>\n          <td>type</td>\n          <td>string</td>\n          <td>text</td>\n          <td>\n            This can be either text or input (supply content slot for input)\n          </td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n</template>\n\n<script lang=\"ts\">\nimport { Component, Vue } from \"vue-property-decorator\";\n\nimport DemoSection from \"./../components/DemoSection.vue\";\nimport Guard from \"./../components/Guard.vue\";\nimport GuardNew from \"./../components/GuardNew.vue\";\nimport GuardsCode from \"./Guards.vue?raw\";\nimport TextInput from \"./../components/TextInput.vue\";\n\n@Component({\n  components: {\n    DemoSection,\n    Guard,\n    GuardNew,\n    TextInput\n  }\n})\nexport default class Accordions extends Vue {\n  demoCode = GuardsCode;\n  textInputPlaceholder = \"placeholder\";\n  textInputValue = \"This is something super secret...\";\n}\n</script>\n\n<style lang=\"less\">\n@import (reference) \"./../styles/Imports\";\n\n.guard-margin {\n  margin-bottom: 10px;\n}\n</style>\n"