
<div
  class="s-guard"
  :class="{ 's-guard--hidden': !visible }"
  @click="showText"
>
  <div class="s-guard__placeholder">
    <i class="icon-lock"></i>
    {{ placeholder }}
  </div>

  <div class="s-guard__text">
    <TextInput
      readonly
      type="text"
      v-model="value"
      @focus="checkSelectedText"
      @keydown.space.prevent="showText"
    />
  </div>
</div>
