
<div :class="guardClasses" @click="showText">
  <div v-if="visible" :class="`${prefix}__text`">
    <i class="fas fa-lock"></i>
    {{ placeholder }}
  </div>
  <div
    :class="
      visible
        ? `${prefix}-wrapper`
        : `${prefix}-wrapper ${prefix}-wrapper--readable`
    "
  >
    <slot v-if="$slots.content" name="content" />
    <div v-else>{{ value }}</div>
  </div>
</div>
